import React from "react";
import {
  MDBJumbotron,
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBIcon,
  MDBCardTitle,
  MDBCard,
  MDBCardBody,
  MDBCardText,
  MDBAnimation,
  MDBNavLink
} from "mdbreact";

import SectionContainer from "../components/sectionContainer";
import Data from "../data/Data";

class PricesPage extends React.Component {
  scrollToTop = () => window.scrollTo(0, 0);

  state = {
    data: Data,
  };

  componentDidMount() {
    document.title = "  - ";
  }

  render() {
    const { data } = this.state;
    return (
      <div className="prices-page">
        <MDBContainer className="mt-5">
          <MDBRow>
            <MDBCol>
              <SectionContainer noBorder>
                <MDBJumbotron style={{ padding: 0 }}>
                  <MDBCol
                    className="background-image text-center py-0 px-4"
                    style={{ backgroundImage: `url(./images/courses.jpg)` }}
                  >
                    <MDBCol className="py-5 jumbo-col">
                      <p className="mb-2">{data.pricesList}</p>

                      <p className="mb-2">{data.pricesList1}</p>

                      <p>{data.pricesList2}</p>
                    </MDBCol>
                  </MDBCol>
                </MDBJumbotron>
              </SectionContainer>
            </MDBCol>
          </MDBRow>

          <MDBRow>
            <MDBCol>
              <MDBJumbotron
                className="text-white text-center p-0 mb-5"
                style={{
                  backgroundImage: `url(https://mdbootstrap.com/img/Photos/Others/gradient1.jpg)`,
                }}
              >
                <MDBCardTitle className="h1-responsive py-4 m-0 font-bold">
                        :
                </MDBCardTitle>
                <div className="content-wrapper">
                  <ul className="translation-list">
                    <li>       ;</li>
                    <li>
                          ,  
                         ;
                    </li>
                    <li>
                         ,    ,
                      . ;
                    </li>
                    <li>    .</li>
                  </ul>
                  <p className="mx-5 mb-3">
                             
                     ,    -,  
                         1800   .
                  </p>
                </div>

                <MDBNavLink
                  tag="button"
                  to="/contacts"
                  className="contact-link btn btn-outline-white mb-5"
                  onClick={this.scrollToTop}
                >
                  <MDBIcon icon="clone" className="mr-2" /> 
                </MDBNavLink>
              </MDBJumbotron>
            </MDBCol>
          </MDBRow>

          <MDBRow className="mb-4">
            <MDBCol md="6" className="tranlsation-item">
              <MDBAnimation reveal type="fadeInRight">
                <MDBCard cascade className="">
                  <MDBCardBody cascade className="text-center">
                    <MDBCardTitle>
                      <strong> :</strong>
                    </MDBCardTitle>
                    <MDBCardText>
                      <ul class="text-left">
                        <li>  - 5 </li>
                        <li>  - 2 </li>
                        <li>  - 1 </li>
                      </ul>
                    </MDBCardText>
                  </MDBCardBody>
                </MDBCard>
              </MDBAnimation>
            </MDBCol>

            <MDBCol md="6" className="tranlsation-item">
              <MDBAnimation reveal type="fadeInRight">
                <MDBCard cascade className="">
                  <MDBCardBody cascade className="text-center">
                    <MDBCardTitle>
                      <strong>  :</strong>
                    </MDBCardTitle>
                    <MDBCardText>
                      <ul class="text-left">
                        <li>     </li>
                        <li>    </li>
                        <li>     </li>
                      </ul>
                    </MDBCardText>
                  </MDBCardBody>
                </MDBCard>
              </MDBAnimation>
            </MDBCol>
          </MDBRow>
        </MDBContainer>
      </div>
    );
  }
}

export default PricesPage;
